import React from "react";

import { Row, Column } from "components/Grid.tsx";
import ExternalLinkPrimaryButton from "components/reusable/atoms/buttons/ExternalLinkPrimaryButton";
import JetBlueTrueBlue from "assets/img/travel/jetblue-trueblue.png";

import { partnersProps } from "components/travel/messages";
import {
  Container,
  TopInner,
  Title,
  Grid,
  PartnerTitle,
  LogoContainer,
  LogoWrapper,
  Logo,
  Caption,
  JetBlueWrapper,
  JetBlueLogo,
} from "./elements";

const { title, button, partners } = partnersProps;

const PartnersGrid = ({ partners }) => (
  <Grid>
    {partners.map(({ name, logos }, partnerIndex) => (
      <React.Fragment key={name}>
        <PartnerTitle>{name}</PartnerTitle>
        {logos.map(({ imgSrc, height, caption }, logoIndex) => (
          <LogoContainer
            key={caption}
            hideBorderBottom={logoIndex === logos.length - 1}
            hideBorderRight={partnerIndex === partners.length - 1}
          >
            <LogoWrapper>
              <Logo src={imgSrc} height={height} />
            </LogoWrapper>
            <Caption>{caption}</Caption>
          </LogoContainer>
        ))}
      </React.Fragment>
    ))}
  </Grid>
);

const JetBlueTile = () => (
  <JetBlueWrapper>
    <JetBlueLogo src={JetBlueTrueBlue} />
    <span>JETBLUE  TRUEBLUE</span>
  </JetBlueWrapper>
);

const Partners = () => (
  <Container id="miles">
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <TopInner>
          <Title>{title}</Title>
          <ExternalLinkPrimaryButton href={button.url}>
            {button.text}
          </ExternalLinkPrimaryButton>
        </TopInner>
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
    <Row padding={30} gutterWidth={26}>
      <Column span={1} spanMedium={0} />
      <Column span={10} spanMedium={12}>
        <JetBlueTile />
        <PartnersGrid partners={partners} />
      </Column>
      <Column span={1} spanMedium={0} />
    </Row>
  </Container>
);

export default Partners;
